<template>
  <main class="grid container" id="app" v-cloak>
    <div class="sub_category_page">
      <div class="column_right column_right_products_container">
        <div class="product__area">
          <!--TEMPLATE-->
          <div v-for="(product, index) in products" :id="index" :key="index">
            <div class="products_page">
              <div class="product product_horizontal">
                <span class="product_code">Код: {{ product.code }}</span>
                <div class="product_status_tooltip_container">
                  <span class="product_status">Наличие </span>
                </div>
                <div class="product_photo">
                  <a href="#" class="url--link product__link">
                    <img :src="product.primaryImageUrl" />
                  </a>
                </div>
                <div class="product_description">
                  <a href="#" class="product__link">{{ getTitle(product) }}</a>
                </div>
                <div class="product_tags hidden-sm">
                  <p>Могут понадобиться:</p>
                  <a
                    v-for="(assoc, index) in product.assocProducts.split('\n')"
                    href="#"
                    class="url--link"
                    :key="index"
                    >{{ getAssoc(assoc) }}</a
                  >
                </div>
                <div class="product_units">
                  <div class="unit--wrapper">
                    <div
                      class="unit--select-btn"
                      :class="{ 'unit--active': btnID[index] }"
                      @click="btnToggle(index)"
                    >
                      За {{ getUnitAlt(product) }}
                    </div>
                    <div
                      class="unit--select-btn"
                      :class="{ 'unit--active': btnAltID[index] }"
                      @click="btnAltToggle(index)"
                      v-show="product.unit !== product.unitAlt"
                    >
                      За {{ getUnitName(product) }}
                    </div>
                  </div>
                </div>
                <p class="product_price_club_card">
                  <span class="product_price_club_card_text"
                    >По карте<br />клуба</span
                  >
                  <span class="goldPrice">{{ getGold(product, index) }}</span>
                  <span class="rouble__i black__i">
                    <svg
                      version="1.0"
                      id="rouble__b"
                      xmlns="http://www.w3.org/2000/svg"
                      x="0"
                      y="0"
                      width="30px"
                      height="22px"
                      viewBox="0 0 50 50"
                      enable-background="new 0 0 50 50"
                      xml:space="preserve"
                    >
                      <use
                        xmlns:xlink="http://www.w3.org/1999/xlink"
                        xlink:href="#rouble_black"
                      ></use>
                    </svg>
                  </span>
                </p>
                <p class="product_price_default">
                  <span class="retailPrice">{{
                    getRetail(product, index)
                  }}</span>
                  <span class="rouble__i black__i">
                    <svg
                      version="1.0"
                      id="rouble__g"
                      xmlns="http://www.w3.org/2000/svg"
                      x="0"
                      y="0"
                      width="30px"
                      height="22px"
                      viewBox="0 0 50 50"
                      enable-background="new 0 0 50 50"
                      xml:space="preserve"
                    >
                      <use
                        xmlns:xlink="http://www.w3.org/1999/xlink"
                        xlink:href="#rouble_gray"
                      ></use>
                    </svg>
                  </span>
                </p>
                <div class="product_price_points">
                  <p class="ng-binding">Можно купить за 231,75 балла</p>
                </div>
                <div class="list--unit-padd"></div>
                <div class="list--unit-desc">
                  <div class="unit--info">
                    <div class="unit--desc-i"></div>
                    <div class="unit--desc-t">
                      <p>
                        <span class="ng-binding"
                          >Продается {{ getUnitFull(product) }}</span
                        >
                        <span class="unit--infoInn">{{
                          getUnitRatio(product)
                        }}</span>
                      </p>
                    </div>
                  </div>
                </div>
                <div class="product__wrapper">
                  <div class="product_count_wrapper">
                    <div class="stepper">
                      <input
                        class="product__count stepper-input"
                        type="number"
                        :value="counterID[index]"
                        @input="updateValue(index, $event)"
                        @wheel="$event.target.blur()"
                      />
                      <span
                        class="stepper-arrow up"
                        :class="{ disabled: increaseIsDisabled[index] }"
                        @click="increaseCounter(index)"
                      ></span>
                      <span
                        class="stepper-arrow down"
                        :class="{ disabled: decreaseIsDisabled[index] }"
                        @click="decreaseCounter(index)"
                      ></span>
                    </div>
                  </div>
                  <span
                    class="btn btn_cart"
                    data-url="/cart/"
                    :data-product-id="product.productId"
                    @click.prevent="addToCart(product, index)"
                  >
                    <svg class="ic ic_cart">
                      <use
                        xmlns:xlink="http://www.w3.org/1999/xlink"
                        xlink:href="#cart"
                      ></use>
                    </svg>
                    <span class="ng-binding">В корзину</span>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <!--TEMPLATE-->

          <Icons />
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Icons from "./components/Icons.vue";

export default {
  name: "App",
  components: {
    Icons,
  },

  data() {
    return {
      counter: 0,
      products: {},
      idCounter: 0,
      btnID: [],
      btnAltID: [],
      counterID: [],
      increaseIsDisabled: {},
      decreaseIsDisabled: {},
      cart: [],
    };
  },

  methods: {
    changeUrl() {
      for (let i = 0; i < this.products.length; i++) {
        this.products[i].primaryImageUrl =
          this.products[i].primaryImageUrl.slice(0, -4) +
          "_220x220_1" +
          this.products[i].primaryImageUrl.slice(-4);
      }
    },

    getIDs() {
      for (let i = 0; i < this.products.length; i++) {
        this.btnID[i] = true;
        this.btnAltID[i] = false;
        this.counterID[i] = 1;
        this.increaseIsDisabled[i] = false;
        this.decreaseIsDisabled[i] = true;
      }
    },

    btnToggle(index) {
      this.$set(this.btnAltID, index, false);
      this.$set(this.btnID, index, true);
    },

    btnAltToggle(index) {
      this.$set(this.btnID, index, false);
      this.$set(this.btnAltID, index, true);
    },

    getTitle(product) {
      return product.title;
    },

    getAssoc(assoc) {
      return ` ${assoc}`;
    },

    getGold(product, index) {
      return this.btnID[index]
        ? (product.priceGoldAlt * this.counterID[index]).toFixed(2)
        : (product.priceGold * this.counterID[index]).toFixed(2);
    },

    getRetail(product, index) {
      return this.btnID[index]
        ? (product.priceRetailAlt * this.counterID[index]).toFixed(2)
        : (product.priceRetail * this.counterID[index]).toFixed(2);
    },

    getUnitFull(product) {
      return product.unitFull === "метр погонный"
        ? "метрами погонными"
        : product.unitFull + "ми:";
    },

    getUnitAlt(product) {
      return ` ${product.unitAlt}`;
    },

    getUnitName(product) {
      return product.unitFull === "метр погонный"
        ? ` ${product.unitFull}`
        : ` ${product.unitFull.slice(0, -1)}у`;
    },

    getUnitRatio(product) {
      return product.unit === "шт."
        ? product.unitRatio + " " + product.unit
        : product.unitRatio +
            " " +
            product.unit +
            " = " +
            product.unitRatioAlt +
            " " +
            product.unitAlt;
    },

    increaseCounter(index) {
      this.decreaseIsDisabled[index] = false;
      if (this.counterID[index] < 99) {
        this.$set(this.counterID, index, Number(this.counterID[index]) + 1);
      }

      if (this.counterID[index] === 99) {
        this.increaseIsDisabled[index] = true;
      }
    },

    decreaseCounter(index) {
      this.increaseIsDisabled[index] = false;

      if (this.counterID[index] > 1) {
        this.$set(this.counterID, index, Number(this.counterID[index]) - 1);
      }

      if (this.counterID[index] === 1) {
        this.decreaseIsDisabled[index] = true;
      }
    },

    addToCart(product, index) {
      const item = {
        id: product.productId,
        amount: this.counterID[index],
      };

      this.cart.push(item);
      alert("Товар добавлен в корзину");
    },

    updateValue(index, event) {
      const value = event.target.value;
      if (String(value).length <= 2) {
        this.$set(this.counterID, index, value);
      }

      if (this.counterID[index].isNan || this.counterID[index] > 99) {
        this.$set(this.counterID, index, 1);
      }

      if (Number(value).isNan || Number(value) < 1) {
        this.$set(this.counterID, index, 1);
      }

      switch (Number(this.counterID[index])) {
        case 1:
          this.decreaseIsDisabled[index] = true;
          this.increaseIsDisabled[index] = false;
          break;
        case 99:
          this.increaseIsDisabled[index] = true;
          this.decreaseIsDisabled[index] = false;
          break;
        default:
          this.decreaseIsDisabled[index] = false;
          this.increaseIsDisabled[index] = false;
          break
      }

      this.$forceUpdate();
    },
  },

  created() {
    fetch("products.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then((response) => response.json())
      .then((response) => {
        this.products = response;
        this.changeUrl();
        this.getIDs();
      });
  },
};
</script>

<style lang="scss">
@import "src/assets/scss/style.scss";
</style>


